
import React from "react";
import { Button } from "react-bootstrap";
import { Collapse } from "react-bootstrap";
import { Well } from "react-bootstrap";
import { Table } from "react-bootstrap";
import { Checkbox } from "react-bootstrap";



class Payment extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      open: false
    };
  }

  render() {
    return <div>
        <Button onClick={() => this.setState({ open: !this.state.open })}>
          Fitness Plans
        </Button>
        <Collapse in={this.state.open}>
          <div>
            <Well>
              <div>
                <h3 className="text-center">Payment Plans</h3>
                <Table responsive>
                  <thead>
                    <tr>
                      <th>Description</th>
                      <th>Price</th>
                      <th>Add</th>
                      
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>Unlimited</td>
                      <td>$60.00 per month</td>
                      <td>
                        <Checkbox checked readOnly>
                          Checkbox
                        </Checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>8 Pack</td>
                      <td>$50.00</td>
                      <td>
                        <Checkbox checked readOnly>
                          Checkbox
                        </Checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>Per Class</td>
                      <td>$7.00 per class</td>
                      <td>
                        <Checkbox checked readOnly>
                          Checkbox
                        </Checkbox>
                      </td>
                    </tr>
                    <Payment/><Button href = "https://venmo.com/Elizabeth-Ricks-3">PAY HERE</Button>
                  </tbody>
                </Table>
              </div>
            </Well>
          </div>
        </Collapse>
      </div>;
  }
}

export default Payment;







    


      



